.footer {
	padding: 20px 0 40px;
	background-color: $blue;
	&-link {
		display: inline-block;
		vertical-align: top;
		font-family: $got;
		@include fs(14);
		color: #fff;
		text-decoration: none;
		transition: .25s;
		&:hover {
			color: $orange;
			text-decoration: none; } }
	.container {
		display: flex;
		flex-wrap: wrap;
 }		// flex-direction: column
	&-logo {
		width: 25%;
		&-link {
			text-decoration: none;
			&__img {
				max-width: 185px;
				width: 100%;
				height: auto; } } }
	&-contacts {
		width: 25%;
		.footer-link {
			margin-bottom: 20px; } }
	&-address {
		display: block;
		font-style: normal;
		color: #fff; }
	&-nav {
		width: 50%;
		&-list {
			text-align: right;
			&-li {
				margin-bottom: 12px;
				display: block;
				&:last-of-type {
					margin-bottom: 0; }
				// min-width: 145px
				// text-align: left
				&:nth-of-type(1),
				&:nth-of-type(2),
				&:nth-of-type(3) {
					min-width: 185px;
					display: inline-block; } } } }
	&-copy {
		width: 25%;
		align-self: flex-end; } }
