.screen-map {
	position: relative;
	padding: 40px 0;
	width: 100%;
	overflow: hidden;
	.container {
		// position: relative
 }		// z-index: 2
	&-contacts {
		position: relative;
		z-index: 2;
		box-sizing: border-box;
		padding: 60px 24px;
		width: 270px;
		text-align: center;
		background-color: rgba(#fff, .8);
		&__link {
			display: inline-block;
			margin-bottom: 25px;
			@include fs(16);
			font-weight: 700;
			text-decoration: none;
			color: $blue;
			transition: .25s;
			&:hover {
				color: $pink;
				text-decoration: underline; } }
		&__address {
			display: block;
			margin-bottom: 80px;
			@include fs(16);
			font-weight: 700;
			font-style: normal;
			text-decoration: none;
			color: $blue; } } }

#map {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
	width: 100%;
	height: 100%; }
