.screen-features {
	@include image('features-bg.jpg');
	@include cover;
	&-title {
		margin-bottom: 25px;
		padding: 30px 0 25px;
		@include image('features-title-bg.svg');
		background-size: 100% 100%;
		.container {
			text-align: center; }
		&__title {
			margin: 0;
			text-align: center;
			font-family: $const;
			@include fs(27);
			line-height: normal;
			letter-spacing: 0.04em;
			font-weight: 700;
			font-syle: italic;
			color: $blue; } } }

.features-list {
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	padding-bottom: 20px;
	&-li {
		margin-bottom: 45px;
		box-sizing: border-box;
		padding-right: 8%;
		padding-left: 17%;
		min-height: 100px;
		width: 50%;
		&:nth-of-type(odd) {
			padding-right: 17%;
			padding-left: 8%;
			text-align: right; }
		&__title {
			margin-bottom: 10px;
			font-family: $const;
			@include fs(22);
			line-height: normal;
			font-weight: 700;
			font-style: italic; }
		&__text {
			@include fs(14);
			@include lh(14, 20);
			letter-spacing: 0.04em; } } }
