.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100vh;
  overflow: auto;
  background-color: rgba(34, 18, 54, 0.7);
  opacity: 0;
  transition: .25s;
}

.overlay.is-active {
  opacity: 1;
  z-index: 100;
}

.overlay__close {
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 40px;
  font-size: 0;
  cursor: pointer;
  border: 0;
  background-color: transparent;
  outline: none;
}

.overlay__close::before, .overlay__close::after {
  content: '';
  position: absolute;
  width: 30px;
  height: 3px;
  background-color: #ba5d8c;
  transition: .25s;
}

.overlay__close::before {
  top: 50%;
  left: 0;
  transform: rotate(45deg);
}

.overlay__close::after {
  top: 50%;
  left: 0;
  transform: rotate(-45deg);
}

.overlay__close:hover::before, .overlay__close:hover::after {
  background-color: #e79074;
}

.overlay-content {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 0;
  width: 100%;
  min-height: 100vh;
  box-sizing: border-box;
}

